/* open-sans-300 - cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: local(""),
    url("../fonts/open-sans-v23-cyrillic-300.woff2") format("woff2"),
    url("../fonts/open-sans-v23-cyrillic-300.woff") format("woff");
  font-display: swap;
}

